* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 62.5%;
}
header {
  width: 100%;
  height: 5rem;
  background: #f1c40f;
}
.logo_div {
  height: 100%;
  width: 20rem;
  /* background: chartreuse; */
  display: flex;
  justify-content: center;
  align-items: center;
}
.logo_div h1 {
  font-size: 2rem;
  color: #fff;
  filter: drop-shadow(2px 3px 5px rgba(0, 0, 0, 0.3));
}
.create_note {
  width: 100%;
  /* background: red; */
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem 0 2rem 0;
}
.create_note .row {
  width: 30%;
  /* height: 100%; */
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  /* background: #f1c40f; */
  box-shadow: 3px 3px 10px 3px rgba(0, 0, 0, 0.2);
  border-radius: 0.5rem;
  position: relative;
}
input {
  width: 100%;
  height: 3rem;
  border: none;
  outline: none;
  padding: 0.5rem 0.5rem 0 0.5rem;
  font-size: 1.2rem;
  font-weight: 600;
}
textarea {
  width: 100%;
  height: auto;
  border: none;
  outline: none;
  padding: 0.1rem 0.5rem 0 0.6rem;
  font-size: 1.3rem;
  resize: vertical;
  margin-bottom: 2rem;
}
button {
  background: #f1c40f;
  color: #fff;
  border: none;
  outline: none;
  position: absolute;
  width: 2.1rem;
  height: 2.1rem;
  bottom: 0;
  right: 0;
  transform: translate(-1rem, 1rem);
  border-radius: 50%;
  box-shadow: 2px 2px 5px 3px rgba(0, 0, 0, 0.2);
}
button:hover {
  background: #fff;
  color: #f1c40f;
}
.notes {
  width: 15%;
  /* background: #efefef; */
  padding: 0.2rem 0.2rem;
  display: inline-block;
  word-break: break-all;
  padding: 1rem;
  margin: 1rem;
  box-shadow: 2px 2px 5px 3px rgba(0, 0, 0, 0.2);
  border-radius: 0.2rem;
  position: relative;
}
.notes p {
  font-size: 15px;
}
.delbtn {
  width: 2rem;
  height: 2rem;
  position: absolute;
  background-color: #fff;
  color: #f1c40f;
  bottom: 0;
  right: 0;
  /* border: none;
  outline: none;  */
  border-radius: 50%;
  box-shadow: 2px 2px 5px 3px rgba(0, 0, 0, 0.2);
  padding: 0.2rem;
}
.delbtn:hover {
  color: red;
}

footer {
  width: 100%;
  height: 5rem;
  background: #f1c40f;
  margin-top: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 22.5%;
}
footer p {
  font-size: 1rem;
  filter: drop-shadow(2px 3px 7px rgba(0, 0, 0, 0.7));
}

@media (max-width: 768px) {
  .create_note .row {
    width: 80%;
  }
  input {
    width: 100%;
    height: 3rem;
  }
  textarea {
    width: 100%;
    height: 6rem;
  }
  .notes {
    width: 85%;
    padding: 0.2rem 0.2rem;
    display: block;
    word-break: break-all;
    padding: 1rem;
    margin: 1rem;
    box-shadow: 2px 2px 5px 3px rgba(0, 0, 0, 0.2);
    border-radius: 0.2rem;
    position: relative;
  }
}
